/* ==========================================================================
    Banner
========================================================================== */

.c-banner {
    position: relative;
    margin-bottom: $gutter;
    padding-left: $gutter;
    padding-right: $gutter;

    // screen at or above 600px
    @include media(">=small") {}

    // screen at or above 900px
    @include media(">=medium") {
        margin-bottom: 0;
    }

    // screen at or above 1200px
    @include media(">=large") {}

    // Elements
    .u-title {
        margin-bottom: 0;
    }
    .c-button {
        margin-top: 1em;
    }
}

/**
 * === DEFAULT ===
 */
.c-banner.is-default {
    padding-top: $gutter;
    padding-bottom: $gutter;

    // No media of any kind, just text
    &.no-image {
        padding-top: $gutter-xs;
        padding-bottom: $gutter-xs;
        background-color: transparent;

        // screen at or above 900px
        @include media(">=medium") {
            padding-top: $gutter-sm;
            padding-bottom: $gutter-xs;
        }
    }
}

/**
 * === LOCATION BANNER ===
 */
.c-banner.is-location {
    @include padding();
    // background-color: var(--bg-color, $white--dark);
    // background-image: url("/wp-content/themes/artprize/assets/img/noisy.png");
    // background-repeat: repeat;
    // background-blend-mode: multiply;
    color: var(--primary-color, $black--light);

    .c-banner__title {
        font-weight: $font-heavy;
        color: var(--primary-color, $black--light);
    }

    .c-banner__subtitle {
        font-weight: $font-bold;
        color: var(--secondary-color, $white--dark);
    }

    .c-button-group {
        margin-top: $gutter-xl;
        padding: $gutter-sm;
        background-color: var(--primary-color, $black--light);
        background-image: url("/wp-content/themes/artprize/assets/img/cardboard.png");
        background-blend-mode: multiply;
        color: var(--bg-color, $white--dark);

        // screen at or above 1200px
        @include media(">=large") {
            padding: $gutter;
        }
    }

    .u-stretch-text {
        margin: 0;
        font-weight: inherit;
        line-height: 1em;
    }

    // screen at or above 900px
    @include media(">=medium") {
        display: flex;
        flex-wrap: wrap;
        column-gap: $gutter;

        .c-banner__title {
            margin-bottom: $gutter;
        }

        .c-banner__title,
        .c-banner__subtitle {
            flex: 1 1 100%;
            text-align: center;
            text-transform: uppercase;
        }

        .c-banner__text,
        .c-banner__media {
            flex: 0 1 50%;
            max-width: calc(50% - $gutter-sm);
        }

        .c-banner__text {
            font-size: $font-base-lg;
        }
    }

    // screen at or above 1200px
    @include media(">=large") {
        column-gap: $gutter-lg;

        .c-banner__text,
        .c-banner__media {
            max-width: calc(50% - $gutter);
        }
    }
}

/**
 * === SWIPER SLIDES ===
 */
.c-banner.is-slides {

    // screen at or above 900px
    @include media(">=medium") {
        display: flex;
        align-items: center;
        column-gap: $gutter;
    }
}

/**
 * === CONTENT ===
 */

.c-banner__text {
    position: relative;
    margin-bottom: $gutter;
    z-index: 1;

    // screen at or above 900px
        @include media(">=medium") {
            margin-bottom: 0;
        }

    // Default Banner
    .c-banner.is-default & {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }

    // Slideshow Banner
    .c-banner.is-slides & {
        flex: 0 1 50%;
        max-width: calc(50% - $gutter-sm);
        padding-top: $gutter;
    }

    // Elements
    .u-title,
    .u-headline,
    .u-subheadline {
        display: block;
    }
}

/**
 * === IMAGE / VIDEO ===
 */

.c-banner__media {
    position: relative;
    // background-image: url("/wp-content/themes/artprize/assets/img/noisy.png");
    // background-blend-mode: multiply;

    // Default Banner
    .c-banner.is-default {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    // Slideshow Banner
    .c-banner.is-slides & {
        flex: 0 1 50%;
        max-width: calc(50% - $gutter-sm);
    }

    .c-media {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
